/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import EnhancedTableHead from '../components/EnhancedTableHead';
import EnhancedTableToolbar from '../components/EnhancedToolBar';
import {
    Checkbox,
    Container,
    IconButton,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableRow,
    TextField,
    Typography
} from '@material-ui/core';
import AnimalSelect from '../components/AnimalSelect';
import {animalOptions, inspectionOptions} from '../staticData/calculator'
import DeleteIcon from '@material-ui/icons/Delete';
import CreateIcon from '@material-ui/icons/Create';
import DoneIcon from '@material-ui/icons/Done';
import WarningIcon from '@material-ui/icons/Warning';
import Header from "../components/Header/Header";


const headCells = [
    { id: 'animal', numeric: false, disablePadding: true, label: 'Animal Type' },
    { id: 'count', numeric: true, disablePadding: false, label: 'Count' },
    { id: 'inspectionType', numeric: false, disablePadding: false, label: 'Inspection Type' },
    { id: 'particulars', numeric: false, disablePadding: false, label: 'Details' },
    { id: 'beef', numeric: false, disablePadding: false, label: 'Beef Promotion Fee' },
    { id: 'predator', numeric: false, disablePadding: false, label: 'Predator Fee' },
    { id: 'subtotal', numeric: false, disablePadding: false, label: 'Subtotal' },
    { id: 'actions', numeric: false, disablePadding: false, label: 'Actions' },
];

const subHeadCells = {
    predator: ['subtotal', 'total'],
    beef: ['subtotal', 'total'],
}

export default class Calculator extends React.Component {

    constructor() {
        super()
        this.state = {
            isEdit: {},
            crossSpicesError: null,
            rows: [],
            selectedAnimal: {},
            selectedInspection: {},
            count: {},
            selected: [],
            page: 0,
            rowsPerPage: 5,
            inspectionTypes: [],
            open: false
        }
    }

    changeInspectionType = (animal, id) => {
        // console.log('elementId', elementId)
        Object.keys(inspectionOptions).forEach(key => {
            if (key === animal) {
                this.setState(prevState => ({
                    inspectionTypes: {
                        ...prevState.inspectionTypes,
                        [id]: Object.keys(inspectionOptions[animal])
                    }
                }))
            }
        });
        this.setState(prevState => ({
            selectedInspection: {
                ...prevState.selectedInspection,
                [id]: Object.keys(inspectionOptions[animal])[0],
            }
        }))
    };

    createData = (id) => {
        return { id }
    };

    createRow = () => {
        const id = Math.random();
        const newRow = this.createData(id);
        const rowsClone = this.state.rows.slice();
        rowsClone.push(newRow);
        this.setState(prevState => ({
            rows: rowsClone,
            selectedAnimal: {
                ...prevState.selectedAnimal,
                [id]: animalOptions[0]
            },
            count: {
                ...prevState.count,
                [id]: 1,

            },
            isEdit: {
                ...prevState.isEdit,
                [id]: true
            }
        }))
        this.changeInspectionType(animalOptions[0], id)
        // console.log('create row,', this.state)
    };



    handleCheckbox = (event, name) => {
        const selectedIndex = this.state.selected.indexOf(name);
        let newSelected = [];
        if (selectedIndex === -1) {
            newSelected = newSelected.concat(this.state.selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(this.state.selected.slice(1));
        } else if (selectedIndex === this.state.selected.length - 1) {
            newSelected = newSelected.concat(this.state.selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                this.state.selected.slice(0, selectedIndex),
                this.state.selected.slice(selectedIndex + 1),
            );
        }
        this.setState({
            selected: newSelected
        })
    };

    handleAnimalSelection = (event, id) => {
        event.persist();
        this.setState(prevState => ({
            selectedAnimal: {
                ...prevState.selectedAnimal,
                [id]: event.target.value,
                [event.target.name]: event.target.value
            }
        }))
        this.changeInspectionType(event.target.value, id)
    };

    handleInspectionTypeSelection = (event, id) => {
        event.persist();
        // console.log('change inspection type', event.target)
        this.setState(prevState => ({
            selectedInspection: {
                ...prevState.selectedInspection,
                [id]: event.target.value,
                [event.target.name]: event.target.value
            }
        }))
    };

    onChangeCount = (event, id) => {
        event.persist();
        this.setState(prevState => ({
            count: {
                ...prevState.count,
                [id]: event.target.value,
                [event.target.id]: event.target.value
            }
        }))
    };

    getInspectionDetails = id => {
        const availableInspectionsForAnimalType = inspectionOptions[this.state.selectedAnimal[id]];
        return availableInspectionsForAnimalType && availableInspectionsForAnimalType[this.state.selectedInspection[id]];
    };

    getParticular = id => (this.getInspectionDetails(id).description);

    getSubtotal = id => {
        const rules = this.getInspectionDetails(id);
        const animalCount = this.state.count[id];
        let animalsToBillFor = animalCount;
        let perAnimalFee = rules.perAnimalFee;
        let subtotal;
        const minimumFeeApplicable = !!(rules.minimumFee && rules.inspectionFee);
        if (rules.perAnimalFeeAfterFirst && animalCount > 0) {
            subtotal = rules.visitFee;
            if (animalCount > rules.perAnimalFeeAfterFirst) {
                const adjustedAnimalCount = animalCount - rules.perAnimalFeeAfterFirst;
                animalsToBillFor = adjustedAnimalCount > 0 ? adjustedAnimalCount : 0;
                subtotal += (animalsToBillFor * perAnimalFee);
            }
        } else {
            if (minimumFeeApplicable && animalCount > 0) {
                subtotal = rules.minimumFee;
                if (animalCount <= rules.minimumFee) {
                    perAnimalFee -= rules.inspectionFee;
                }
                subtotal += (animalsToBillFor * perAnimalFee);
            } else {
                subtotal = rules.visitFee;  // if theres is no minimum fee but inspection fee is still there
                subtotal += (animalsToBillFor * perAnimalFee);
            }
        }
        if ((this.state.selectedAnimal[id]).toLowerCase() === 'elk' && this.state.selected.includes(id)) {
            subtotal = rules.visitFee;
        }
        // console.log('rules', rules, animalsToBillFor, subtotal);
        return subtotal;
        // return subtotal > rules.minimumFee ? subtotal : rules.minimumFee;
    };

    getExtraSubtotal = (id, category) => {
        const rules = this.getInspectionDetails(id);
        const animalCount = this.state.count[id];
        let animalsToBillFor = animalCount;
        if (rules && rules.perAnimalFeeAfterFirst) {
            const adjustedAnimalCount = animalCount - rules.perAnimalFeeAfterFirst;
            animalsToBillFor = adjustedAnimalCount > 0 ? adjustedAnimalCount : 0;
        }
        const extraFees = rules[category] ? rules[category] : 0;
        return {
            animalsToBillFor,
            extraFees
        }
    }

    sum = (accumulator, row) => (accumulator + this.getSubtotal(row.id));

    getTotal = (rows) => (rows.length > 0 ? rows.reduce(this.sum, 0) : 0);

    deleteRow = id => {
        const rowsClone = this.state.rows.slice();
        const index = rowsClone.findIndex(row => row.id === id);
        if (index > -1) {
            rowsClone.splice(index, 1);
        }
        this.setState({
            rows: rowsClone
        })
    };

    save = id => {
        this.setState(prevState => ({
            isEdit: {
                ...prevState.isEdit,
                [id]: false
            }
        }))

        if (id !== this.state.rows[0].id) {
            this.checkSpecies()
        }
    }

    checkSpecies = () => {
        if (this.state.rows.length > 1) {
            const cross = this.state.rows.filter(row => this.state.selectedAnimal[row.id] === this.state.selectedAnimal[this.state.rows[0].id])
            // console.log('cross', cross)
            if (cross.length !== this.state.rows.length) {
                // console.log('throw an error')
                this.setState({
                    crossSpicesError: `Cross species are not allowed, Please remove the row or change specie to '${this.state.selectedAnimal[this.state.rows[0].id]}'`
                })
            } else {
                this.setState({
                    crossSpicesError: null
                })
            }
        } else {
            this.setState({
                crossSpicesError: null
            })
        }
    }

    edit = id => {
        this.setState(prevState => ({
            isEdit: {
                ...prevState.isEdit,
                [id]: true
            }
        }))
    }

    handleOpenNavBar = () => {
        this.setState({open:!this.state.open})
    }

    isSelected = name => this.state.selected.indexOf(name) !== -1;

    render() {
        return (
            <div id="st-container" className={this.state.open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
                <Header
                    header="Calculations Sandbox"
                    openNavBar={this.handleOpenNavBar}
                    open={this.state.open} />
                <Container maxWidth='xl' style={{ padding: '88px 20px 20px', maxWidth: 1100 }}>
                    {this.state.crossSpicesError &&
                    <Typography className={'error'} id='error'>
                        <WarningIcon color='inherit' style={{ marginRight: '5px' }} /> {this.state.crossSpicesError}
                    </Typography>
                    }
                    <div className={'root'}>
                        <Paper className={'paper'}>
                            <EnhancedTableToolbar createRow={this.createRow} />
                            <TableContainer>
                                <Table
                                    className={'table'}
                                    aria-labelledby="tableTitle"
                                    size={'medium'}
                                    aria-label="enhanced table"
                                >
                                    <EnhancedTableHead
                                        headCells={headCells}
                                        subHeadCells={subHeadCells}
                                    />
                                    <TableBody>
                                        {this.state.rows && this.state.rows.length > 0 &&
                                        this.state.rows.map((row, index) => {
                                            const isItemSelected = this.isSelected(row.id);
                                            const labelId = `enhanced-table-checkbox-${index}`;
                                            const predator_fee_data = this.getExtraSubtotal(row.id, 'predatorFee')
                                            const predator_animalCount = predator_fee_data.animalsToBillFor;
                                            const predator_extraFee_perAnimal = predator_fee_data.extraFees;
                                            const predator_fee_subtotal = predator_animalCount * predator_extraFee_perAnimal
                                            const beefPromotion_fee_data = this.getExtraSubtotal(row.id, 'beefPromotionFee')
                                            const beefPromotion_animalCount = beefPromotion_fee_data.animalsToBillFor;
                                            const beefPromotion_extraFee_perAnimal = beefPromotion_fee_data.extraFees;
                                            const beefPromotion_fee_subtotal = beefPromotion_animalCount * beefPromotion_extraFee_perAnimal

                                            return (
                                                <TableRow
                                                    data-testid='row'
                                                    hover
                                                    key={row.id}
                                                >
                                                    <TableCell padding="checkbox"
                                                               className={!this.state.isEdit[row.id] ? 'disable' : null} >
                                                        {(this.state.selectedAnimal[row.id]).toLowerCase() === 'elk' && <Checkbox
                                                            checked={isItemSelected}
                                                            onClick={event => this.handleCheckbox(event, row.id)}
                                                            inputProps={{ 'aria-labelledby': labelId }}
                                                        />}
                                                    </TableCell>
                                                    <TableCell>
                                                        <AnimalSelect
                                                            name={`animal_select${index + 1}`}
                                                            className={!this.state.isEdit[row.id] ? 'disable' : null}
                                                            onChange={(e) => this.handleAnimalSelection(e, row.id)}
                                                            value={this.state.selectedAnimal[row.id]}
                                                            options={animalOptions} />
                                                    </TableCell>
                                                    <TableCell>
                                                        <TextField
                                                            id={`count_input${index + 1}`}
                                                            InputProps={{ inputProps: { min: 0 } }}
                                                            className={!this.state.isEdit[row.id] ? 'disable' : null}
                                                            value={this.state.count[row.id]}
                                                            onChange={(e) => this.onChangeCount(e, row.id)}
                                                            type='number' />
                                                    </TableCell>
                                                    <TableCell>
                                                        <AnimalSelect
                                                            onChange={(e) => this.handleInspectionTypeSelection(e, row.id)}
                                                            value={this.state.selectedInspection[row.id]}
                                                            name={`inspection_select${index + 1}`}
                                                            className={!this.state.isEdit[row.id] ? 'disable' : null}
                                                            options={this.state.inspectionTypes[row.id]} />
                                                    </TableCell>
                                                    <TableCell>
                                                        <div className={'fixedBox'}>
                                                            {this.getParticular(row.id)}
                                                        </div>
                                                    </TableCell>
                                                    <TableCell>
                                                        {beefPromotion_animalCount} * {beefPromotion_extraFee_perAnimal}
                                                    </TableCell>
                                                    <TableCell>
                                                        ${beefPromotion_fee_subtotal}
                                                    </TableCell>
                                                    <TableCell>
                                                        {predator_animalCount} * {predator_extraFee_perAnimal}
                                                    </TableCell>
                                                    <TableCell>
                                                        ${predator_fee_subtotal}
                                                    </TableCell>
                                                    <TableCell id={`subtotal${index + 1}`}>
                                                        ${this.getSubtotal(row.id)}
                                                    </TableCell>
                                                    <TableCell>
                                                        <IconButton
                                                            aria-label="delete row"
                                                            className={`icons`}
                                                            onClick={() => this.deleteRow(row.id)}
                                                        >
                                                            <DeleteIcon />
                                                        </IconButton>
                                                        {!this.state.isEdit[row.id] ? <IconButton
                                                                className={`icons floatRight`}
                                                                aria-label="edit row"
                                                                onClick={() => this.edit(row.id)}
                                                            >
                                                                <CreateIcon />
                                                            </IconButton> :
                                                            <IconButton
                                                                className={`icons floatRight`}
                                                                aria-label="save row"
                                                                id={`saveRow${index + 1}`}
                                                                onClick={() => this.save(row.id)}
                                                            >
                                                                <DoneIcon />
                                                            </IconButton>
                                                        }
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                        {this.state.rows && this.state.rows.length > 0 &&
                                        <TableRow>
                                            <TableCell colSpan={9}>
                                                Total
                                            </TableCell>
                                            <TableCell colSpan={3}>
                                                $ {this.getTotal(this.state.rows)}
                                            </TableCell>
                                        </TableRow>}
                                    </TableBody>
                                </Table>
                            </TableContainer>

                        </Paper>
                    </div>
                </Container>
            </div>
        );
    }
}