import React from 'react'
import { NativeSelect } from '@material-ui/core'
import PropTypes from 'prop-types';


export default function AnimalSelect({ options, ...props}) {
    return (
        <NativeSelect
            {...props}
            fullWidth
        >
            {options && options.map((animal, animalIndex) => (
                <option value={animal} key={animalIndex}>{animal}</option>
            ))}
        </NativeSelect>
    )
}

AnimalSelect.propTypes = {
    options: PropTypes.array.isRequired,
};
