import React from 'react'
import { TableHead, TableRow, TableCell } from '@material-ui/core';
// import PropTypes from 'prop-types';


function EnhancedTableHead(props) {
    const { headCells, subHeadCells } = props;

    return (
        <TableHead>
            <TableRow style={{ verticalAlign : 'bottom'}}>
                <TableCell
                    padding="checkbox">
                    Released No Charge
                </TableCell>
                {headCells.map(headCell => (
                    <TableCell
                        key={headCell.id}
                        colSpan={(headCell.id === 'predator' || headCell.id === 'beef') ? 2: 1}
                        align={'left'}
                        padding={'default'}
                    >
                        {headCell.label}
                    </TableCell>
                ))}
            </TableRow>
            <TableRow style={{ verticalAlign: 'bottom' }}>
                <TableCell colSpan={5}
                           padding="checkbox">
                </TableCell>
                {Object.keys(subHeadCells).map((title) => (
                    subHeadCells[title].length > 0 && subHeadCells[title].map((subHeader, i) => (
                        <TableCell key={i}>
                            {subHeader}
                        </TableCell>
                    ))
                ))}
                <TableCell colSpan={2}
                           padding="checkbox">
                </TableCell>
            </TableRow>
        </TableHead>
    );
}

// EnhancedTableHead.propTypes = {
//     classes: PropTypes.object.isRequired,
//     order: PropTypes.oneOf(['asc', 'desc']).isRequired,
//     orderBy: PropTypes.string.isRequired,
//     headCells: PropTypes.array.isRequired
// };

export default EnhancedTableHead