import React from 'react'
import { makeStyles, lighten, Toolbar, Typography, IconButton } from '@material-ui/core';
import clsx from 'clsx';
import AddIcon from '@material-ui/icons/Add';

const useToolbarStyles = makeStyles(theme => ({
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        flex: '1 1 100%',
    },
    tableTitle: {
        fontWeight: 'bold'
    }
}));

function EnhancedTableToolbar({ createRow }) {
    const classes = useToolbarStyles();
    return (
        <Toolbar>
            <Typography className={clsx(classes.title, classes.tableTitle)} variant="h5" id="tableTitle">
                Price Calculator for Brand Inspection
            </Typography>
            {/* <Tooltip title="Add New Row"> */}
            <IconButton aria-label="Add New Row" onClick={createRow} data-testid='create-row-button' id='add_row_button'>
                <AddIcon />
            </IconButton>
            {/* </Tooltip> */}
        </Toolbar>
    );
}
export default EnhancedTableToolbar
